/* App.css */

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Header */
#header {
  background-color: #1c1c1c;
  padding: 20px 0;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
}

#header h1 a {
  color: #ffffff;
  font-size: 32px;
  text-decoration: none;
}

#header h2 {
  color: #ffffff;
  font-size: 18px;
  margin-top: 4px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar ul {
  list-style: none;
  display: flex;
  margin: 0;
}

.navbar li {
  margin: 0 20px;
}

.navbar a {
  color: #ffffff;
  text-decoration: none;
  font-size: 18px;
  transition: 0.3s;
}

.navbar a:hover {
  color: #e44d26;
}

/* Sections */
.container {
  width: 80%;
  margin: 0 auto;
}

.section-title h2 {
  font-size: 36px;
  margin-bottom: 20px;
}

.section-title p {
  font-size: 18px;
  color: #777;
}

/* ... (other sections styles) */

/* Footer */
footer {
  background-color: #1c1c1c;
  color: #ffffff;
  text-align: center;
  padding: 20px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
}
